import {
    Group,
    PlaneGeometry,
    Mesh,
} from 'three';

import Common from "@/graphics/Common";
import Input from "@/graphics/Input";
import Assets from '@/graphics/Assets';

import {
  DEFAULT_ACTIVITY,
  BUGGED_ITEM
} from "@/constants/app";

import {
  bracketMaterial,
  prefabBoxMaterial,
  prefabInfoBoxMaterial
} from "./Prefabs";

import { Glyph } from 'three-glyph';

export default class Panel extends Group {
    PARAMS = {
    }
    constructor({ texture, color, title, description, activity }) {
      super();
      this.title = title;
      this.description = description;
      if (activity) this.activity = activity;
      const geometry = new PlaneGeometry(22/194, 1, 1);
      const material = bracketMaterial.clone(true);
      material.uniforms.color.value = color;
      const bracket = new Mesh( geometry, material );
      bracket.position.x = -0.5 -0.06;
      bracket.renderOrder = 2;
      this.add(bracket);

      const boxGeometry = new PlaneGeometry(1, 1, 1);
      const boxMaterial = prefabBoxMaterial.clone()
      boxMaterial.uniforms.tDiffuse.value = texture;
      boxMaterial.uniforms.background.value = Assets.fui;
      boxMaterial.uniforms.timeOffset.value = Math.random() * 10.0;
      const box = new Mesh( boxGeometry, boxMaterial );
      box.position.x = 0;
      this.add(box);
      const infoboxGeometry = new PlaneGeometry(4, 1, 1);
      const infoMaterial = prefabInfoBoxMaterial.clone();
      infoMaterial.uniforms.opacity.value = 1;
      
      this.info = new Group();
      this.infoBox = new Mesh( infoboxGeometry,  infoMaterial);
      this.info.visible = false;
      this.info.add(this.infoBox);
      this.add(this.info);
      this.info.position.x = -(1 + 22/194 + 1.5);

      const scale = 0.00825;
      this.glyph = new Glyph({
        text: `${this.title} - ${this.description} (press E)`,
        font: Assets.PPNeueMontrealMedium,
        material: Assets.glyphMaterial,
        width: 460
      });
      this.glyph.position.x = -(2 - 0.1);
      this.glyph.position.y = 0.5 - 0.1;
      this.glyph.matrixAutoUpdate = false;
      this.glyph.mesh.matrixAutoUpdate = false;
      this.glyph.scale.set(scale, scale, scale);
      this.glyph.alignTop();
      this.glyph.alignLeft();
      // this.glyph3.position.y = -1.5;
      this.glyph.updateMatrix();
      this.glyph.mesh.updateMatrix();
      this.info.add(this.glyph);
      this.glyph.mesh.renderOrder = 3;
      this.glyph.mesh.material.depthTest = false;
      

    }
    handleRaycast(output) {
        output;
        // console.log("raycastPanel");
        Common.raycaster.setFromCamera(Input.coords, Common.camera);
        const intersects = Common.raycaster.intersectObjects(this.children, false);

        if (intersects.length > 0) {
          if (output.active != this) {
            output.active = this;
          }
          this.children[0].material.uniforms.selected.value = 1;
          this.children[1].material.uniforms.selected.value = 1;
          this.children[1].material.uniforms.opacity.value = 1;
          if (!this.description) {
            output.activity = BUGGED_ITEM;
          } else {
            this.info.visible = true;
            output.activity = DEFAULT_ACTIVITY;
          }
          // document.body.style.cursor = 'pointer';
        } else {
          this.info.visible = false;
          this.children[0].material.uniforms.selected.value = 0;
          this.children[1].material.uniforms.selected.value = 0;
          this.children[1].material.uniforms.opacity.value = 0.5;

          if (output.active === this) {
            output.active = null;
            output.activity = DEFAULT_ACTIVITY;
          }
        }
    }
    render(t) {
      t;
      if (this.children[1].material.uniforms.selected.value === 1) {
        this.children[1].material.uniforms.timeOffset.value += 0.0628;
      }
      // this.children[1].material.uniforms.time.value = t;
    }
    dispose() {
    }
    resize() {
    }
  }
  