import {
    Group,
    Color,
} from 'three';
import Assets from "@/graphics/Assets";
import Panel from "@/graphics/UI/Panel";

// import { DEFAULT_ACTIVITY } from "@/constants/app";
// Omamori - rotection but can't afford Trauma Team insurance.
// Ham and Mushroom Pizza -	Pizza's a lot like your health – you don't realize the value of it until it's gone.
export default class Inventory extends Group {
    PARAMS = {
    }
    constructor() {
        super();

        const data = [
            {
                texture: Assets.omamori,
                // color: new Color(0xf0b537)
                color: new Color(0x1ded83),
                title: "Omamori",
                description: "When you want protection but can't afford health insurance.",
                activity: () => {
                    window.open("https://en.wikipedia.org/wiki/Omamori");
                }
            },
            {
                texture: Assets.counterfeit,
                color: new Color(0x9d2bf5),
                title: "Curriculum vitae",
                description: "A relic from the pre-cyber age, this data-packed piece of junk used to be the key to unlocking your next gig. Now, it's just a useless hunk of paper. Might be worth a few eddies to a desperate netrunner, but don't bet on it.",
                activity: () => {
                    window.open("/assets/misc/CV_TRAN_REMI_2024.pdf");
                }
            },
            {
                texture: Assets.pizza,
                color: new Color(0x2570d4),
                title: "Ham and Mushroom Pizza",
                description: "Pizza's a lot like your health – you don't realize the value of it until it's gone.",
                activity: () => {
                    window.open("https://www.youtube.com/shorts/6Akh2ngOJf0");
                }
            },
            {
                texture: Assets.quickhack,
                color: new Color(0xf0b537),
                title: "Quickhack",
                // color: new Color(0x1ded83)
            },
            {
                texture: Assets.arakasa,
                color: new Color(0xf0b537),
                title: "Arakasa"
                // color: new Color(0x1ded83)
            },
        ]
        data.forEach((attrs, i) => {
            i;
            const panel = new Panel({
                ...attrs
            });
            this.add(panel);
            panel.position.y = i - data.length * 0.5 + 0.5;
        })
        this.position.x = 4.5;
    }
    render(t) {
        this.children.forEach(mesh => {
            mesh.render(t);
        })
    }
    dispose() {
    }
    resize() {
    }
  }
  